import React from 'react';
import Header from './Header';
import Footer from './Footer';
class Layout extends React.Component {
    render() {
        return (
            <div>
                <div className="header">
                    <Header />
                </div>
                <div className="main">
                   
                </div>
                <div className="footer">
                    <Footer />
                </div>
            </div>
        );
    }
}

export default Layout;